body,
html {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Bogle Regular";
}

@font-face {
  font-family: "Bogle Regular";
  font-style: normal;
  font-weight: normal;
  src: local("Bogle Regular"),
    url("./styles/fonts/Bogle/BogleWeb-Regular.ttf") format("truetype");
}
